.App {
  text-align: center;
}

h2 {
  color: #111;
  font-family: "Roboto", "Helvetica", "Arial", sans-serif;
  font-size: 275px;
  font-weight: bold;
  letter-spacing: -1px;
  line-height: 1;
  text-align: center;
}

h3 {
  color: #111;
  font-family: "Roboto", "Helvetica", "Arial", sans-serif;
  font-size: 30px;
  font-weight: 300;
  line-height: 32px;
  margin: 0 0 12px;
  text-align: center;
}

p {
  /* color: #685206; */
  font-family: "Roboto", "Helvetica", "Arial", sans-serif;
  font-size: 14px;
  line-height: 24px;
  margin: 0 0 24px;
  text-align: justify;
  text-justify: inter-word;
}

div,
span {
  font-family: "Roboto", "Helvetica", "Arial", sans-serif;
}